.other_block {
    width: 50%;
    height: auto;
    background-color: white;
    border-radius: 20px;
    margin-left: 20px;
    margin-right: 50px;
}

.other_block_two {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
}

@media (min-width: 50px) and (max-width: 1200px) {
     .other_block {
         display: none;
     }
}
